@import url(https://fonts.googleapis.com/css2?family=Teko:wght@600;700&display=swap);
* {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Jura', 'Roboto', 'Helvetica Neue',
    sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-color: #EDEAE5;
  overflow-x:hidden;
}


.slideanim {visibility:hidden;}
  .slide {
    animation-name: slide;
    -webkit-animation-name: slide;
    animation-duration: 1s;
    -webkit-animation-duration: 1s;
    visibility: visible;
  }
  @keyframes slide {
    0% {
      opacity: 0;
      -webkit-transform: translateY(70%);
              transform: translateY(70%);
    } 
    100% {
      opacity: 1;
      -webkit-transform: translateY(0%);
              transform: translateY(0%);
    }
  }
  @-webkit-keyframes slide {
    0% {
      opacity: 0;
      -webkit-transform: translateY(70%);
    } 
    100% {
      opacity: 1;
      -webkit-transform: translateY(0%);
    }
  }
  #tekofont {
    font-family: 'Teko', sans-serif!important;
  }

/*contact.js*/
#image {
    position:relative;
    cursor: pointer;
    padding:0px;
}

#btn {
    position:relative;
    top:50%;
    left:50%;
    -ms-tranfrom: translate(-50%, -50%);
    -webkit-transform:translate(-50%, -50%);
            transform:translate(-50%, -50%);
    text-align:center;
    cursor: pointer;
}

#imageoverlay {
    position:absolute;
    background-color:#EDEAE5;
    bottom:0;
    left:0;
    right:0;
    opacity:0;
    overflow:hidden;
    width:100%; 
    height:100%;
    transition:.5s ease;
    cursor: pointer;
}
#image:hover #imageoverlay {
    opacity: 0.8;
    cursor: pointer;
}
