}

#chips {
    position:relative;
    top:50%;
    left:50%;
    -ms-tranfrom: translate(-50%, -50%);
    -webkit-transform:translate(-50%, -50%);
            transform:translate(-50%, -50%);
    text-align:center;
    cursor: pointer;
}

#overlay {
    position:absolute;
    bottom:0;
    left:0;
    right:0;
    opacity:0.8;
    overflow:hidden;
    width:100%; 
